<template>
  <div>
    <v-layout wrap>
      <v-flex xs12>
        <v-text-field color="#13D8F2" label="Nombre" :rules="[rules.required]" v-model="name"></v-text-field>
      </v-flex>

      <v-flex xs12>
        <v-text-field color="#13D8F2" label="Apellido Paterno"></v-text-field>
      </v-flex>

      <v-flex xs12>
        <v-text-field color="#13D8F2" label="Apellido Materno"></v-text-field>
      </v-flex>

      <v-flex>
        <v-menu>
          <v-text-field slot="activator" color="#13D8F2" label="Fecha de Nacimiento" :value="date"></v-text-field>
          <v-date-picker v-model="date"></v-date-picker>
        </v-menu>
      </v-flex>

      <v-flex>
        <v-text-field
          color="#13D8F2"
          label="Celular"
          type="tel"
          v-model="phone"
          :rules="[rules.required, rules.number]"
        ></v-text-field>
      </v-flex>

      <v-flex xs12>
        <v-text-field color="#13D8F2" label="Edad"></v-text-field>
      </v-flex>

      <v-flex xs12>
        <v-text-field
          color="#13D8F2"
          label="Correo electrónico"
          type="email"
          :rules="[rules.required, rules.email]"
          v-model="email"
        ></v-text-field>
      </v-flex>

      <v-flex xs12 v-if="status == 1">
        <v-textarea
          color="#13D8F2"
          label="Mensaje"
          type="message"
          :rules="[rules.required]"
          v-model="message"
        ></v-textarea>
      </v-flex>

      <v-flex xs12 v-if="status == 2">
        <v-text-field
          color="#13D8F2"
          label="Contraseña"
          :type="show ? 'text' : 'password'"
          :append-icon="show ? 'visibility' : 'visibility_off'"
          @click:append="show = !show"
          :rules="[rules.required]"
          v-model="password"
        ></v-text-field>
      </v-flex>

      <v-flex xs12>
        <v-text-field
          color="#13D8F2"
          label="Contraseña"
          :type="show ? 'text' : 'password'"
          :append-icon="show ? 'visibility' : 'visibility_off'"
          @click:append="show = !show"
          :rules="[rules.required]"
          v-model="password"
        ></v-text-field>

        <v-flex xs12>
          <v-text-field color="#13D8F2" label="RFC"></v-text-field>
        </v-flex>

        <v-flex xs12>
          <v-text-field color="#13D8F2" label="Curp"></v-text-field>
        </v-flex>

        <v-card-actions style="padding-right: 40px;">
          <v-spacer></v-spacer>
          <!--
            EMITO UN EVENTO AL COMPONENTE PADRE: ESTOY EN UN COMPONTENTE "NIETO"
            POR LO QUE ESTE EMIT ENCADENARA OTRO EMIT PARA QUE EL COMPONENTE PADRE
            CAPTURE ESTE EVENTO Y CIERRE EL DIALOG
          -->
          <v-btn color="#13D8F2" @click="$emit('RequestEventCloseDialog')">Volver</v-btn>
          <v-btn type="submit" color="#13D8F2" @click="$emit('RequestEnableTab')">Enviar</v-btn>
        </v-card-actions>-

      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // datos del usuario
      name: "",
      email: "",
      phone: "",
      password: "",
      date: "",

      message: "",
      alert: false,
      show: false,
      rules: {
        required: value => !!value || "Requerido.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Correo no valido.";
        },
        number: value => {
          const pattern = /^([0-9])*$/;
          return pattern.test(value) || "Teléfono invalido.";
        }
      },

      sending: false,
      response: ""
    };
  }
};
</script>

