<template>
  <div>
    <v-text-field
      class="no-margin"
      prefix="$"
      v-model="number"
      label="Valor Del Inmueble"
      color="#13D8F2"
    ></v-text-field>
  </div>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      valorDelInmueble: "",
      number: ""
    };
  },
  methods: {
    actualizarMontoDeCredito() {},
    onInput() {
      this.actualizarMontoDeCreditoPorModificacion(0);
      this.actualizarModificarMontoDeCredito(false);
    },
    currencyInput() {
      let number = parseFloat(this.number.replace(/,/g, "") || 0);
      this.valorDelInmueble = number;
      this.number = this.numberWithCommas(parseFloat(number));
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    ...mapMutations([
      "actualizarValorDelInmueble",
      "actualizarModificarMontoDeCredito",
      "actualizarMontoDeCreditoPorModificacion"
    ])
  },
  watch: {
    valorDelInmueble() {
      this.actualizarValorDelInmueble(this.valorDelInmueble);
      this.onInput();
    },
    number() {
      this.currencyInput();
    }
  }
};
</script>
